.card {
    background-color: #999;
    color: #fff;
    text-align: center;
    margin:10px;
    padding: 10px;
    font-size: 3rem;
}
.card--on {
    background-color: darkslategrey
}
.items {
    padding: 0;
    max-width: 800px;
    margin: 0 auto;
    list-style: none;
    display: flex;
    flex-flow: row wrap;
}
.item {
    margin:0;
    padding: 0;
    width: 200px;
    /* height:400px; */
}
.status-bar {
    text-align: center;
    font-size: 1.5rem;
}
